import {reactive,toRefs,computed,watch,watchEffect,onMounted,defineComponent} from 'vue';
import {useRouter} from 'vue-router';
import {getRequest, postRequest} from '../../request/api';
import url from '../../request/url';
import {token} from '../../libs/util';
import BgBox from '../../components/BgBox';
import OrderList from '../../components/OrderList';
import OrderItem from '../../components/OrderItem';
import { Toast } from 'vant';
export default defineComponent({
    name:'order',
    components:{
        BgBox,
        OrderList,
        OrderItem
    },
    setup () {
        let state = reactive({
            test: "2020-02-12T06:09:25.000Z",
            tabIndex: 0,
            tabData: [{ title: "全部" }, { title: "进行中" }, { title: "已完成" }],
            orderData: [],
            isHas: true,
        })
        //计算属性
        let decimal = computed(() => (v, n = 2) => v.toFixed(n));

        let formatDate = computed(() => {
            return (v, format) => {
                let date = new Date(v);
                //格式化年份
                let year = date.getFullYear().toString();
                if (/(y+)/.test(format)) {
                    let content = RegExp.$1;
                    format = format.replace(content, year.slice(year.length - content.length));
                }
                let o = {
                    M: date.getMonth() + 1,
                    d: date.getDate(),
                    h: date.getHours(),
                    m: date.getMinutes(),
                    s: date.getSeconds()
                };
                for (let key in o) {
                    let reg = new RegExp(`(${key}+)`);
                    if (reg.test(format)) {
                        let groupContent = RegExp.$1;
                        format = format.replace(groupContent, o[key] >= 10 ? o[key] : groupContent.length == 2 ? '0' + o[key] : o[key]);
                    }
                }
                return format;
            }
        })

        let {back,getOrderDataByStatus,changeTab,confirm,removeOrder} = methods(state);

        onMounted(() => {
            getOrderDataByStatus();
        })

       /* watch(() => [state.tabIndex,state.test],(to,from) => {
            console.log("to==>",to);
            console.log("from==>",from);
        },{
            immediate:true,//表示渲染后，立即触发一次
            deep:true//表示深度监听对象里面的子属性（被侦听的内容需要是函数的写法）
        })*/

        /*watchEffect(() => {
            console.log("state.tabIndex==>",state.tabIndex);//ref  类型的需要加.value
        })*/

        return {
            ...toRefs(state),
            decimal,
            formatDate,
            back,
            changeTab,
            confirm,
            removeOrder
        }
    }
})

function methods(state){
    let tokenString = token('token');
    let router = useRouter();

    let back = () => {
        router.back();
    }

    //根据订单状态获取订单数据
    let getOrderDataByStatus = () => {
        getRequest(url.findOrder,{status:state.tabIndex}).then(({code,result}) => {
            if(code == 70000){
                if (state.tabIndex == 0 && result.length == 0) return Reflect.set(state,'isHas',false);
                let orderDatas = [];
                result.map(item => {
                    let o = Object.create(null);
                    Reflect.set(o,'oid',item.oid);
                    Reflect.set(o,'count',item.count);
                    Reflect.set(o,'total',item.count * item.price);
                    Reflect.set(o,'status',item.status);
                    Reflect.set(o,'date',item.createdAt);
                    Reflect.set(o,'data',[item]);
                    orderDatas.push(o);
                })
                Reflect.set(state,'orderData',orderDatas);
            }
        }).catch((err) => {
            console.log("err==>",err);
        });
    }

    let changeTab = () => {
        getOrderDataByStatus();
    }

    //提交商品
    let confirm = ({oid},index) => {
        postRequest(url.receive,{oid}).then(({code,msg}) => {
            if(code == '80000'){
                if(state.tabIndex == 1) state.orderData.splice(index,1);
                Reflect.set(item,'status',2);
                Toast(msg);
            }
        }).catch((err) => {
            console.log("err==>",err);
        });
    }
    //删除商品
    let removeOrder = ({oid},index) => {
        postRequest(url.removeOrder,{oid}).then(({code,msg}) => {
            if(code == 90000) state.orderData.splice(index,1);
            Toast(msg);
        }).catch((err) => {
            console.log("err==>",err);
        });
    }

    return{
        back,
        getOrderDataByStatus,
        changeTab,
        confirm,
        removeOrder
    }
}