<template>
    <div class="order">
        <van-nav-bar title="我的订单" left-text="返回" left-arrow fixed @click-left="back" />
        <bg-box>
            <div v-if="isHas">
                <van-tabs v-model:active="tabIndex" color="#0C34BA" title-active-color="#0C34BA" line-height="2" @change="changeTab">
                    <van-tab :title="item.title" v-for="(item, index) in tabData" :key="index">
                        <div v-if="orderData.length > 0">
                            <div class="order-box" v-for="(v, k) in orderData" :key="v.oid">
                                <order-list
                                    :info-title-left="v.oid"
                                    :date="formatDate(v.date,'yyyy-MM-dd hh:mm:ss')"
                                    :count="v.count"
                                    :total="decimal(v.total)"
                                    :is-right="true"
                                    @confirm="confirm(v, k)"
                                    @remove="removeOrder(v, k)"
                                    :is-receive="v.status == 2"
                                >
                                    <order-item  v-for="(value, idx) in v.data" :item="value" :key="idx"></order-item>
                                </order-list>
                            </div>
                        </div>
                        <div v-else>
                            <van-empty description="没有订单数据" />
                        </div>
                    </van-tab>
                </van-tabs>
            </div>
            <div v-else>
                <van-empty description="没有订单数据" />
            </div>
        </bg-box>
    </div>
</template>

<script>
    export { default } from './order';
</script>

<style lang="less" scoped>
    @import './order.less';
</style>